:root {
  --blue: #54c0eb;
  --black: #071109;
  --white: #ffffff;
  --grey: #e3e3e2;
  --light-grey: #f9f9f9;
  --yellow: #ffd15c;
  --tomato: #ff6257;
  --tomato-alpha: #ff625710;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  display: grid;
  place-items: center;
  min-height: 100vh;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
}

.application-container {
  width: 34rem;
  padding: 2rem;
  background-color: var(--white);
  border-radius: 1rem;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.date-display-container, .date-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.date-display-container {
  height: 4rem;
}

.date-display-container > p, .date-section aside > p {
  font-size: 1.5rem;
  text-transform: uppercase;
  letter-spacing: 0.3rem;
}

.date-section {
  gap: 0.5rem;
}

.date-section > p {
  font-size: 4rem;
}

.date-section aside > p:first-of-type {
  letter-spacing: 0.6rem;
}

.add-field-container {
  margin: 1rem auto;
}

.add-field-container label {
  font-style: italic;
  font-size: 1.2rem;
  color: var(--tomato);
  opacity: 0;
  transition: opacity 0.3s ease-out;
}

.add-field-container .single-field-container {
  display: flex;
  column-gap: 2.5%;
  margin-top: 1rem;
}

.add-field-container .single-field-container > input {
  flex-grow: 1;
  padding: 1rem;
  font-size: 1.2rem;
  border: 1px solid var(--grey);
  border-radius: 0.35rem;
  outline: none;
  transition: border 0.3s ease-out;
}

.add-field-container .single-field-container > input:focus {
  border: var(--blue);
}

.add-field-container .single-field-container > button {
  flex-basis: 12%;
  color: var(--white);
  background-color: var(--blue);
  border: none;
  border-radius: 0.35rem;
  outline: none;
  cursor: pointer;
}

.task-display ul {
  list-style: none;
}

.task-display ul > div {
  display: flex;
  margin: 1rem auto;
}

.task-display li {
  flex-grow: 1;
  padding: 1rem;
  font-size: 1.2rem;
  border: 1px solid var(--grey);
  border-radius: 0.35rem 0 0 0.35rem;
  cursor: pointer;
}

.task-display .done {
  background-color: var(--light-grey);
}

.task-display button {
  flex-basis: 12%;
  color: var(--white);
  background-color: var(--tomato);
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 0 0.35rem 0.35rem 0;
}

.pending-tasks-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 3.4rem;
}

.pending-tasks-container > p {
  font-size: 1.2rem;
}

.pending-tasks-container > button {
  padding: 1rem;
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--white);
  background-color: var(--blue);
  border: none;
  border-radius: 0.35rem;
  outline: none;
  cursor: pointer;
}

.error .add-field-container label {
  opacity: 1;
}

.error .add-field-container .single-field-container > input {
  background-color: var(--tomato-alpha);
  border: 1px solid var(--tomato);
}
